/* Initial state of the about-navbar */
.about-navbar {
  position: fixed;
  left: 0;
  top: 0;
  height: 200vh;
  background-color: rgb(112, 125, 161);
  color: white;
  padding: 1.5% 20px 10% 20px;
  transition: all 0.5s ease-in-out; /* Adjust transition duration */
  padding-top: 6rem;
}

.about-navbar ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.about-navbar ul li {
  text-align: left;
  margin-bottom: 10px; /* Adjust spacing between items */
}

.about-navbar ul li a {
  display: flex;
  align-items: center;
  color: white;
  text-decoration: none;
  transition: color 0.3s ease, transform 0.3s ease;
}

.about-navbar ul li a span {
  padding-left: 10px;
}

.about-navbar ul li a:hover {
  color: rgb(161, 161, 92);
  transform: scale(1.1); /* Adjust hover effect */
}

.about-navbar ul li a.active {
  color: rgb(161, 161, 92);
  transform: scale(1.1); /* Adjust active link effect */
}

.toggle-icons {
  margin-bottom: 30px;
  display: flex;
  cursor: pointer;
  font-size: 24px;
  color: white;
}

.scroll-link {
  margin: 30px 0;
}

.about-navbar ul li {
  position: relative; /* Ensure the icon name appears relative to the list item */
}

.icon-container {
  display: flex;
  align-items: center;
  position: relative;
}

.icon-name {
  display: block;
  margin-left: 10px;
  font-size: 18px; 
  color: white;
  background-color: rgb(112, 125, 161); 
  padding: 5px;
  border-radius: 5px;
  position: absolute;
  left: 100%;
  white-space: nowrap;
  transition: opacity 0.3s ease;
}

